.wrapper {
    width: 70%;
    padding-top: 50px;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    overflow: auto;
    margin-left: 450px;
  }
.navbar {
    background: #768f9b;
    height: 100px;
    box-shadow: 0 0 25px 0 black;
  }
  
.hero {
  height: 400px;
  background-size: fill;
  background-position: center;
  background-image: url("https://wallpapercave.com/wp/wp2067177.jpg");
  position: relative;
  box-shadow: 0 0 15px 0 black;
}

.hero::after {
  content: "";
  display: block;
  opacity: .4;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: black;
}

.hero * {
  position: relative;
  z-index: 2;
}

.hero h1, .hero h2 {
  color: white;
}

.hero h1, .hero h2 {
  font-size: 50px;
  padding-top: 200px;
  margin-top: 10px;
}

.hero h6 {
  font-size: 20px;
  padding-top: 50px;
}

.card {
    background: #fff;
    border-radius: 2px;
    height: 200px;
    margin: 1rem;
    position: relative;
    width: 200px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
  }
  .card > .img-container > img {
    width: 60%
  }
  
